.hover-underline-animation {
  display: inline-block;
  position: relative;
  color: #1D3A59;
}

.hover-underline-animation::after {
  content: '';
  position: absolute;
  width: 100%;
  transform: scaleX(0);
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: #027BB1;
  transform-origin: bottom right;
  transition: transform 0.30s ease-out;
}

.hover-underline-animation:hover::after {
  transform: scaleX(1);
  transform-origin: bottom left;
}

/*--------------------------------------------------------------
# carousel
--------------------------------------------------------------*/
.containerr {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 100px 50px;
  padding: 0;
}

.containerr .card {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  width: 350px;
  max-width: 100%;
  height: 300px;
  background: white;
  border-radius: 20px;
  transition: 0.5s;
  box-shadow: 0 35px 80px rgba(0, 0, 0, 0.15);
}

.containerr .card:hover {
  height: 400px;
  cursor: pointer;
}

.containerr .card .img-box {
  position: absolute;
  top: 20px;
  width: 300px;
  height: 220px;
  background: #333;
  border-radius: 12px;
  overflow: hidden;
  transition: 0.5s;
}

.containerr .card:hover .img-box {
  top: -100px;
  scale: 0.75;
  box-shadow: 0 15px 45px rgba(0, 0, 0, 0.2);
}

.containerr .card .img-box img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.containerr .card .content {
  position: absolute;
  top: 252px;
  width: 100%;
  height: 35px;
  padding: 0 30px;
  text-align: center;
  overflow: hidden;
  transition: 0.5s;
}

.containerr .card:hover .content {
  top: 130px;
  height: 250px;
}

.containerr .card .content h2 {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--clr);
}

.containerr .card .content p {
  color: #333;
}

.containerr .card .content a {
  position: relative;
  top: 15px;
  display: inline-block;
  padding: 12px 25px;
  text-decoration: none;
  background: var(--clr);
  color: white;
  font-weight: 500;
}

.containerr .card .content a:hover {
  opacity: 0.8;
}

@media (max-width: 480px) {
  .containerr .card {
    width: 230px;
    border-radius: 15px;
  }

  .containerr .card .img-box {
    width: 185px;
    border-radius: 10px;
  }

  .containerr .card .content p {
    font-size: 0.8rem;
  }

  .containerr .card .content a {
    font-size: 0.9rem;
  }
}

